import { BaseEditor } from "slate";
import { HistoryEditor } from "slate-history";
import { Editable, ReactEditor, Slate } from "slate-react";

export enum CET {
  EDITOR = "editor",
  NUMBER_LIST = "ol",
  NORMAL_LIST = "ul",
  LIST_ITEM = "li",
  DIV = "div",
  Block = "block",
  P = "p",
  H1 = "h1",
  H2 = "h2",
  H3 = "h3",
  H4 = "h4",
  IMG = "img",
  LINK = "link",
  TABLE = "table",
  TBODY = "tbody",
  TR = "tr",
  TD = "td",
  TODOLIST = "todo",
  CODE = "code",
  FILE = "file",
}

export const TextWrappers = [CET.DIV, CET.H1, CET.H2, CET.H3, CET.H4, CET.P];
export const InLineTypes = [CET.IMG, CET.LINK];

export enum Marks {
  BOLD = "bold",
  ITALIC = "italic",
  FontSize = "fontSize",
  Underline = "underline",
  LineThrough = "line-through",
  Color = "color",
  BGColor = "backgroundColor",
  TextAlign = "textAlign",
}

export type CustomElement = {
  type: CET;
  [key: string]: any;
  url?: string; // Link
  fileName?: string; // 
  id?: number; // 
  border?: boolean; // img
  colSpan?: number; // td
  rowSpan?: number; // td 
  width?: number; // td
  height?: number; // td
  selected?: boolean; // td
  canTdEdit?: boolean; // td
  liColor?: string; // li color
  checked?: boolean; // todoList
  wrapperWidthWhenCreated?: number; // tabletd
  defaultMode?: string; // code 
  defaultCode?: string; // code 
  textAlign?: "left" | "right" | "center"; // textWrapper
  tdMap?: any; // tdMap
  children: (CustomText | CustomElement)[];
};
export type CustomText = { text: string; [key: string]: any } & Partial<
  {
    [key in Marks]: any;
  }
>;
export type EditorType = BaseEditor & ReactEditor & HistoryEditor;

export type StateShape = Parameters<typeof Slate>[0]["value"];
export type EditorCompPropShape = {};
export type EditorCompShape = (
  props: EditorCompPropShape
) => React.ReactElement;
export type EditableProps = Parameters<typeof Editable>[0];
